const spells = [
  ['', 'products'],
  ['', 'Alcohol'],
  ['', 'ENTERTAIMENT'],
  ['', 'HEALTH'],
  ['', 'TRANSPORT'],
  ['  ', 'HOUSING'],
  ['', 'Technique'],
  ['  ', 'Communal, communication'],
  ['  ', 'Sports, hobbies'],
  ['', 'Education'],
  ['', 'Other'],
  ['/', 'SALARY'],
  ['. ', 'ADD, INCOME'],
];

const translate = name => {
  for (let key of spells)
    if (key[0].toLowerCase().includes(name.toLowerCase())) return key[1];
  return 'OTHER';
};

export default translate;
